<template>
  <div>

    <vs-input
      data-vv-validate-on="blur"
      name="firstname"
      icon-no-border
      icon="icon icon-user"
      icon-pack="feather"
      label-placeholder="first name"
      v-model="firstname"
      class="w-full"/>
    <span v-if="firstnameError" class="text-danger text-sm">{{ firstnameError }}</span>


    <vs-input
      data-vv-validate-on="blur"
      name="lastname"
      icon-no-border
      icon="icon icon-user"
      icon-pack="feather"
      label-placeholder="lastname"
      v-model="lastname"
      class="w-full mt-8"/>
    <span v-if="lastnameError" class="text-danger text-sm">{{ lastnameError }}</span>


    <vs-input
      data-vv-validate-on="blur"
      name="email"
      icon-no-border
      icon="icon icon-mail"
      icon-pack="feather"
      label-placeholder="email"
      v-model="email"
      class="w-full mt-8"/>
    <span v-if="emailError" class="text-danger text-sm">{{ emailError }}</span>

    <vs-input
      data-vv-validate-on="blur"
      name="password"
      icon-no-border
      icon="icon icon-key"
      icon-pack="feather"
      label-placeholder="password"
      type="password"
      v-model="password"
      class="w-full mt-8"/>
    <span v-if="passwordError" class="text-danger text-sm">{{ passwordError }}</span>

    <v-select class="w-full mt-8" style="width: fit-content; min-width: 250px"
              v-model="selectedEnglishLevel" :options="englishLevels" dir="ltr"/>
    <span v-if="levelError" class="text-danger text-sm">{{ levelError }}</span>

  <br>
    <vs-button  type="border" to="/login" class="float-right mt-6">return to login</vs-button>
    <vs-button id="register-loading" class="float-left mt-6" @click="register"  >register</vs-button>
  </div>
</template>

<script>
import mixins from "../../../../mixins";
import axios from '../../../../http/axios';
import vSelect from "vue-select";

export default {
  mixins: [mixins],
  components: {'v-select': vSelect},
  data () {
    return {
      firstname: '',
      lastname: '',
      email: '',
      password: null,

      englishLevels: [{id: null, label: "select your english level"}],
      selectedEnglishLevel: {id: null, label: "select your english level"},

      firstnameError: null,
      lastnameError: null,
      emailError: null,
      passwordError: null,
      levelError: null,
    }
  },

  mounted () {
    this.getEnglishLevels();
  },

  methods: {
    register(){
      //
      let emailVal = this.validateEmail(this.email);
      let levelVal = this.validateField(this.selectedEnglishLevel.id, "english level", "select");

      this.emailError = emailVal.message;
      this.levelError = levelVal.message;

      if (!emailVal.validation || !levelVal.validation){
        return;
      }

      this.startLoading('register-loading')

      axios.post(`/api/auth/register`, {
        first_name: this.firstname,
        last_name: this.lastname,
        email: this.email,
        password: this.password,
        level: this.selectedEnglishLevel.id,
      },{
        headers:{
          Accept: "application/json",
        }
      }).then(res => {
        if (res.status === 201){
          this.simpleNotify(this.NOTIFY_SUCCESS_TITLE, 'you registered successfully', this.NOTIFY_SUCCESS);
          this.$router.push({
            name: "login",
            params: {
              phone: this.phone
            }
          })
        }else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.stopLoading('register-loading');
      }).catch(err => {
        if (err.response.status === 400){
          this.stopLoading('register-loading');
          if (err.response.data.phone){
            this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'you already registered', this.NOTIFY_DANGER);
          }
        }
      })

    },

    getEnglishLevels() {
      axios.get(`/api/english/english-levels`, {
        headers: {
          Accept: "application/json",
        }
      }).then(res => {
        if (res.status === 200) {
          res.data.forEach(s => {
            this.englishLevels.push({
              id: s.id,
              label: s.title
            })
          })
        } else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
      })
    },
  }
}
</script>
